body {
    margin : 0;
    padding: 0;
}

.nav {
    height            : 70px;
    width             : 100%;
    display           : -webkit-flex;
    display           : flex;
    -webkit-align-items       : center;
            align-items       : center;
    /*background-color: rgb(189, 189, 189);*/
    margin            : 0;
}

.left {
    padding-left   : 30px;
    -webkit-flex           : 50% 1;
            flex           : 50% 1;
    font-size      : 2vw;
    font-family    : Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
    display        : -webkit-flex;
    display        : flex;
    -webkit-align-items    : center;
            align-items    : center;
    
    color          : aliceblue;
}

.searchInput {
    height: 20px;
    width : 15vw;
    border: none;
}

.right {
    -webkit-flex: 20% 1;
            flex: 20% 1;
}

.links {
    display        : -webkit-flex;
    display        : flex;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    font-size      : 1.6vw;
    -webkit-align-items    : center;
            align-items    : center;
}

.link {
    text-decoration: none;
    color          : black;
    font-family    : 'Press Start 2P', cursive;
}

.categories {
    padding-left: 12px;
    height      : 30px;
    width       : 100%;

}

.category {
    height         : 100%;
    display        : -webkit-flex;
    display        : flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items    : center;
            align-items    : center;
    margin-left    : 30%;
    margin-right   : 30%;
    font-size      : 1vw;
}

.mButton {
    height   : 100%;
    width    : 2vw;
    font-size: 34px;
}
.firstClothDiv {
    height: 50%;
    width: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
}
